/* pt-sans-caption-regular - latin_cyrillic */
@font-face {
  font-family: 'PT Sans Caption';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(''),
       url('../fonts/pt-sans-caption-v13-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/pt-sans-caption-v13-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
}
